.discount {
   &__header {
   }
   &__title {
   }
   &__btn {
   }
   &__items {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
   }
   &__item {
      flex: 0 1 45% !important;
      width: 100%;
      margin: 8px !important;
      display: flex;
      align-items: center;
      background: #ffffff;
      border: 1px solid #e8e9ea;
      padding-right: 0 !important;
      @media (max-width: 992px) {
         flex-direction: column;
      }
      @media (max-width: 480px) {
         flex: 0 1 100% !important;
      }
   }
   &__item-img {
      max-width: 278px;
      width: 100%;
      height: 100%;
      position: relative;
      margin-right: 16px;
      img {
         width: 278px;
         object-fit: cover;
         height: auto;
      }
      @media (max-width: 1172px) {
         max-width: 200px;
         img {
            width: 200px;
         }
      }
      @media (max-width: 992px) {
         max-width: 100%;
         margin-right: 0;
         img {
            width: 100%;
         }
      }
   }
   &__item-content {
      padding: 16px;
   }
   &__item-label {
      background: #ffffff;
      border: 2px solid #fbe303;
      padding: 4px 8px;
      color: #171b24;
      position: absolute;
      left: 16px;
      top: 16px;
   }
   &__item-text {
      margin-bottom: 12px;
   }
   &__item-price {
      display: flex;
      align-items: flex-end;
      margin-bottom: 16px;
   }
   &__item-title {
      margin-right: 8px;
      color: #171b24;
      &:nth-child(2) {
         color: #8b8d92;
         text-decoration: line-through;
         text-decoration-color: #2f323a;
      }
   }
   &__item-btn {
      width: 100%;
      display: flex;
      justify-content: center;
      cursor: pointer;
   }
}
