.cabinet {
   max-width: 768px;
   width: 100%;
   margin: 0 auto;
   &__title {
      margin-bottom: 40px;
   }
   &__body {
      background: #ffffff;
      border: 1px solid #d1d1d3;
      padding: 36px;
      @media (max-width: 768px){
         padding: 24px;
      }
      @media (max-width: 400px){
         padding: 24px 10px;
      }
   }
   &__subtitle {
      margin-bottom: 24px;
   }
   &__form {
      display: flex;
      flex-wrap: wrap;
   }
   &__form-item {
      flex: 0 1 100%;
      margin-bottom: 14px;
      &.half {
         flex: 0 1 50%;
         padding-right: 12px;
         @media (max-width: 480px){
            padding-right: 0;
            flex: 0 1 100%;
         }
         &:nth-child(2),
         &:nth-child(4),
         &:nth-child(7) {
            padding-right: 0;
         }
      }
   }
   &__form-label {
      margin-bottom: 8px;
   }
   &__form-input {
      background: #f6f6f6;
      border: none;
      height: 48px;
      border-bottom: 1px solid #d1d1d3;
      width: 100%;
      padding: 12px 16px;
      color: #8b8d92;
      min-width: 100%;
      max-width: 100%;   
      &::placeholder {
         color: #8b8d92;
      }
      &::-moz-placeholder {
         color: #8b8d92;
         opacity: 1;
      }
   }
   &__form-btn {
      padding: 12px 24px !important;
      border: none;
   }
}
