.section {
   &__return {
   }
}
.return {
   margin-top: 40px;
   &__item {
      margin-bottom: 80px;
      display: flex;
      @media (max-width: 600px){
         flex-direction: column;
         margin-bottom: 40px;
      }  
   }
   &__title {
      max-width: 376px;
      min-width: 376px;
      width: 100%;
      margin-right: 16px;
      @media (max-width: 992px){
         font-size: 28px !important;
         min-width: 300px;
      }
      @media (max-width: 768px){
         min-width: 220px;
      }
      @media (max-width: 600px){
         margin-bottom: 20px;
      }
   }
   &__item-content {
   }
   &__text {
      margin-bottom: 22px;
   }
}
