.section {
   &__hero-return{
      background: url(..//img/hero-return.webp) center no-repeat;
   }
   &__hero-deliver{
      background: url(..//img/hero-deliver.webp) center no-repeat;
   }
   &__hero-brands{
      background: url(..//img/hero-brands.webp) center no-repeat;
   }
   &__hero-katalog{
      background: url(..//img/hero-katalog.webp) center no-repeat;
   }
   &__hero-orders{
      background: url(..//img/hero-orders.webp) center no-repeat;
   }
   &__hero-cart{
      background: url(..//img/hero-cart.webp) center no-repeat;
   }
   &__hero {
      background-size: cover !important;
      .swiper {
         .container {
            position: relative;
         }
         &__buttons {
            display: flex;
            align-items: center;
            position: absolute;
            right: 0;
            bottom: 22px;
            z-index: 999;
            @media (max-width: 768px){
               right: 20px;
               bottom: 10px;
            }
         }
         &__btn {
            background: #ffffff;
            border: 1px solid #d1d1d3;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 52px;
            height: 52px;
            cursor: pointer;
            transition: all .3s ease;
            &.swiper-prev {
               margin-right: 12px;
               transform: rotate(180deg);
            }
            path{
               transition: all .3s ease;
            }
            &:hover{
               background: #2F323A;
               transition: all .3s ease;
               path{
                  stroke: #ffffff;
                  transition: all .3s ease;
               }
            }
         }
         .slide {
            height: 420px;
            position: relative;
            .container {
               display: flex;
               align-items: center;
               height: 100%;
            }
            img {
               width: 100%;
               height: 100%;
               object-fit: cover;
               z-index: -1;
               position: absolute;
               left: 0;
               top: 0;
               bottom: 0;
               right: 0;
            }
            &__content {
               max-width: 570px;
               width: 100%;
            }
            &__title {
               color: #171b24;
               margin-bottom: 20px;
            }
            &__text {
               color: #454950;
               margin-bottom: 72px;
            }
            &__btn {
               padding: 12px 24px !important;
            }
         }
      }
   }
}
.hero{
   &__title{
      margin-bottom: 20px;
   }
   &__text{
      max-width: 490px;
      width: 100%;
   }
}
