.nav {
   &.fixed__nav {
      .nav__body {
         position: fixed;
         left: 0;
         top: 0;
         right: 0;
         background: #ffffff;
         z-index: 9999;
         padding: 10px 0;
         transition: all 0.3s ease;
      }
   }
   &.active {
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      z-index: 999;
      background: #f6f6f6;
      @media (max-width: 992px) {
         &.fixed__nav{
            .nav__body{
               position: relative;
            }
         }
         .nav__logo {
            display: none;
         }
         .nav__footer {
            display: block;
            .container {
               justify-content: center;
               .nav__link {
                  margin-bottom: 11px;
               }
            }
         }
         .nav__form {
            display: flex;
         }
         .nav__body > .container {
            justify-content: space-between;
         }
         .nav__body-content {
            display: flex;
            justify-content: space-between;
         }
         .nav__btn {
            width: 100%;
            display: flex;
            justify-content: center;
         }
      }
      @media (max-width: 600px) {
         .nav__body {
            margin: 16px 0;
            .container {
               justify-content: flex-start;
            }
         }
         .nav__body-content {
            flex-direction: column;
            align-items: flex-start;
            width: 100%;
         }
         .nav__form {
            margin: 16px 0;
            max-width: 100%;
         }
         .nav__header-items {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
         }
         .nav__header > .container {
            flex-direction: column;
         }
         .nav__menu {
            display: flex;
            justify-content: center;
            width: 100%;
         }
      }
   }
   &__header {
      background: #f6f6f6;
      padding: 10px 0;
      @media (max-width: 600px){
         padding: 10px 0 0;
      }
      .container {
         display: flex;
         align-items: center;
         justify-content: space-between;
      }
   }
   &__link {
      display: inline-flex;
      align-items: center;
      color: #5d6066;
      transition: all 0.3s ease;
      img {
         margin-right: 8px;
      }
      &:hover {
         color: #2f323a;
         transition: all 0.3s ease;
      }
   }
   &__logo {
      @media (max-width: 400px) {
         width: 140px;
         img {
            width: 100%;
         }
      }
   }
   &__header-items {
      @media (max-width: 600px) {
         display: none;
      }
      .nav__link {
         margin-right: 24px;
         @media (max-width: 768px) {
            margin-right: 12px;
         }
         &:last-child {
            margin-right: 0;
         }
      }
   }
   &__body {
      transition: all 0.3s ease;
      @media (max-width: 992px){
         padding: 10px 0;
      }
      .container {
         display: flex;
         align-items: center;
         justify-content: space-between;
      }
   }
   &__body-content {
      display: flex;
      align-items: center;
   }
   &__btn {
      display: inline-flex;
      align-items: center;
      padding: 12px 21px !important;
      @media (max-width: 992px) {
         margin-right: 16px;
      }
      @media (max-width: 600px){
         margin-right: 0;
      }
      @media (max-width: 480px) {
         padding: 8px 15px !important;
      }
   }
   &__form {
      max-width: 444px;
      width: 100%;
      height: 48px;
      background: #f6f6f6;
      border-bottom: 1px solid #d1d1d3;
      display: flex;
      align-items: center;
      margin: 0 16px;
      @media (max-width: 992px) {
         display: none;
      }
   }
   &__form-input {
      height: 100%;
      width: 100%;
      padding: 0 16px;
      color: #8b8d92;
      border: none;
      background: initial;
      &::placeholder {
         color: #8b8d92;
      }
      &::-moz-placeholder {
         color: #8b8d92;
      }
   }
   &__form-btn {
      height: 100%;
      padding: 0 16px;
      background: initial;
      border: none;
   }
   &__menu {
      display: flex;
      align-items: center;
      @media (max-width: 600px) {
         display: none;
      }
   }
   &__menu-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0 5px;
      img {
         margin-bottom: 5px;
      }
   }
   &__footer {
      padding: 10px 0;
      @media (max-width: 992px) {
         display: none;
      }
      .container {
         display: flex;
         align-items: center;
         justify-content: space-between;
         @media (max-width: 992px) {
            flex-wrap: wrap;
         }
      }
      .nav__link {
         @media (max-width: 992px) {
            margin-right: 30px;
            &:last-child {
               margin-right: 0;
            }
         }
      }
   }
}
.modal {
   background: #ffffff;
   max-width: 572px;
   width: 100%;
   padding: 36px;
   @media (max-width: 480px) {
      padding: 20px;
   }
   &__close {
      position: absolute;
      right: 33px;
      top: 33px;
      width: 26px;
      height: 26px;
      cursor: pointer;
      &::after,
      &::before {
         content: "";
         position: absolute;
         width: 100%;
         height: 2px;
         background: #ffffff;
      }
      &::after {
         transform: rotate(45deg);
      }
      &::before {
         transform: rotate(-45deg);
      }
   }
   &__wrapper {
      position: fixed;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      z-index: 9999; 
      display: flex;
      justify-content: center;
      align-items: center;
      display: none;
      background: rgba(23, 27, 36, 0.6);
      @media (max-width: 600px) {
         padding: 0 10px;
      }
      @media (max-width: 480px) {
         overflow-y: scroll;
         padding: 36px 10px;
         align-items: flex-start;
      }
   }
   &__title {
      text-align: center;
   }
   .nav__form {
      max-width: 100%;
      margin: 24px 0 !important;
      @media (max-width: 992px) {
         display: flex;
      }
   }
   &__menu {
      display: flex;
      justify-content: space-between;
      @media (max-width: 600px) {
         flex-wrap: wrap;
      }
   }
   &__list {
      @media (max-width: 600px) {
         margin-right: 20px;
      }
      li {
         display: flex;
         align-items: center;
         margin-bottom: 16px;
         transition: all 0.3s ease;
         img {
            opacity: 0;
            visibility: hidden;
            margin-right: 4px;
            transition: all 0.3s ease;
         }
         &:hover {
            transition: all 0.3s ease;
            margin-left: 20px;
            img {
               opacity: 1;
               visibility: visible;
               transition: all 0.3s ease;
            }
         }
      }
   }
}
button {
   cursor: pointer;
}
