.section {
   &__notfound {
      padding: 80px 0 !important;
   }
}
.notfound {
   border: 1px solid #D1D1D3;
   max-width: 580px;
   width: 100%;
   margin: 0 auto;
   text-align: center;
   padding: 36px;
   @media (max-width: 400px){
      padding: 24px;
   }
   &__title {
      margin-bottom: 8px;
   }
   &__subtitle {
      margin-bottom: 8px;
   }
   &__text {
      margin-bottom: 24px;
   }
   &__btn {
      padding: 12px 24px !important;
   }
}
