.deliver {
   &__title {
      margin-bottom: 32px;
   }
   &__items {
      display: flex;
      justify-content: space-between;
      margin-bottom: 80px;
      @media (max-width: 992px) {
         flex-wrap: wrap;
      }
   }
   &__item {
      margin-right: 16px;
      @media (max-width: 992px) {
         flex: 0 1 46%;
         margin-bottom: 22px;
      }
      @media (max-width: 480px) {
         flex: 0 1 100%;
         margin-right: 0;
         text-align: center;
      }
      &:last-child {
         margin-right: 0;
      }
   }
   &__item-img {
      margin-bottom: 12px;
   }
   &__body {
      border: 1px solid #d1d1d3;
      padding: 80px 196px;
      @media (max-width: 1172px){
         padding: 80px;
      }
      @media (max-width: 992px){
         padding: 40px;
      }
      @media (max-width: 600px){
         padding: 20px;
      }
      @media (max-width: 400px){
         padding: 20px 10px;
      }
   }
   .accordion {
      &__item {
         background: #F6F6F6;
         margin-bottom: 12px;
         &.show{
            .accordion__content{
               display: block;
            }
            .accordion__header{
               img{
                  transform: rotate(-90deg);
                  transition: all .3s ease;
               }
            }
         }
      }
      &__header {
         padding: 16px 20px;
         display: flex;
         align-items: flex-start;
         justify-content: space-between;
         cursor: pointer;
         @media (max-width: 480px){
            padding: 10px;
         }
         img{
            margin-left: 16px;
            transform: rotate(90deg);
            transition: all .3s ease;
         }
      }
      &__content {
         padding: 0 61px 16px 20px;
         display: none;
         @media (max-width: 600px){
            padding: 0 20px 16px;
         }
         @media (max-width: 480px){
            padding: 0 10px 10px;
         }
      }
      &__text {
         margin-bottom: 20px;
      }
   }
}
