.contact {
   &__title {
      margin-bottom: 40px;
   }
   &__img {
      margin-bottom: 80px;
      width: 100%;
      @media (max-width: 992px){
         margin-bottom: 40px;
      }
      @media (max-width: 768px){
         height: 400px;
         img{
            height: 100%;
            object-fit: cover;
         }
      }
      img{
         width: 100%;
      }
   }
   &__subtitle {
      margin-bottom: 20px;
   }
   &__items {
      display: flex;
      justify-content: space-between;
      @media (max-width: 768px){
         flex-direction: column;
      }
   }
   &__item {
      margin-right: 16px;
      @media (max-width: 768px){
         margin-right: 0;
         margin-bottom: 20px;
      }
      &:last-child{
         margin-right: 0;
      }
   }
   &__text {
   }
}
