footer {
   background: #2f323a;
}
.footer {
   &__body {
      display: flex;
      justify-content: space-between;
      padding: 40px 0;
      border-bottom: 1px solid #5d6066;
      @media (max-width: 768px) {
         flex-direction: column;
         justify-content: center;
         align-items: center;
         padding: 16px 0;
      }
   }
   &__info {
      @media (max-width: 768px) {
         margin-bottom: 30px;
      }
   }
   &__logo {
      img {
         margin-bottom: 22px;
      }
   }
   &__info-item {
      margin-bottom: 16px;
      display: flex;
      align-items: center;
      .footer__text {
         margin-left: 8px;
      }
      svg {
         width: 22px;
      }
      path {
         stroke: #e8e9ea;
      }
      &:last-child {
         margin-bottom: 0;
      }
   }
   &__text {
      color: #e8e9ea !important;
   }
   &__list {
      @media (max-width: 768px) {
         margin-bottom: 30px;
         max-width: 200px;
         width: 100%;
         &:last-child {
            margin-bottom: 0;
         }
      }
      li {
         margin-bottom: 12px;
         display: flex;
         align-items: center;
         @media (max-width: 768px){
            justify-content: center;
         }
         svg {
            opacity: 0;
            visibility: hidden;
            transition: all 0.3s ease;
            margin-right: 8px;
            @media (max-width: 768px){
               display: none;
            }
         }
         path {
            stroke: #fff;
         }
         &:hover {
            svg {
               margin-left: 20px;
               opacity: 1;
               visibility: visible;
               transition: all 0.3s ease;
            }
         }
      }
   }
   &__footer {
      padding: 16px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 768px) {
         flex-direction: column;
         justify-content: center;
         align-items: center;
         text-align: center;
      }
   }
   &__items {
      display: flex;
      align-items: center;
      @media (max-width: 768px) {
         margin: 16px 0;
      }
      img {
         margin-right: 16px;
         &:last-child {
            margin-right: 0;
         }
      }
   }
}
