.section {
   &__reviews {
   }
}
.reviews {
   &__header {
   }
   &__title {
   }
   &__buttons {
      display: flex;
      align-items: center;
   }
   &__btn {
      background: #ffffff;
      border: 1px solid #d1d1d3;
      width: 52px;
      height: 52px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      transition: all 0.3s ease;
      path {
         transition: all 0.3s ease;
      }
      &:hover {
         background: #2f323a;
         transition: all 0.3s ease;
         path {
            stroke: #ffffff;
            transition: all 0.3s ease;
         }
      }
   }
   .swiper-prev {
      transform: rotate(-180deg);
      margin-right: 12px;
   }
   &__swiper {
      .slide {
         padding: 24px;
         @media (max-width: 992px) {
            padding: 16px;
         }
         @media (max-width: 768px) {
            padding: 12px;
         }
         &__header {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            margin-bottom: 12px;
         }
         &__contact {
            display: flex;
            align-items: center;
         }
         &__img {
            margin-right: 12px;
         }
         &__title {
         }
         &__text {
            margin-bottom: 15px;
            height: 280px;
            @media (max-width: 600px) {
               height: auto;
            }
         }
         &__date {
            color: #8b8d92;
         }
      }
   }
}
