.section__kategory {
   &-brands {
      .kategory {
         &__item {
            padding: 0;
            background: #ffffff;
            border: 1px solid #e8e9ea;
            &:hover {
               background: #ffffff;
               border: 1px solid #5d6066;
            }
            img {
               width: 100%;
            }
         }
      }
   }
}
.kategory {
   &__header {
      @media (max-width: 768px) {
         flex-direction: column;
      }
   }
   &__title {
      @media (max-width: 768px) {
         margin-bottom: 16px;
         text-align: center;
      }
   }
   &__items {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
   }
   &__item {
      background: #f6f6f6;
      flex: 0 1 15%;
      margin: 8px;
      padding: 12px 0 0;
      width: 100%;
      text-align: center;
      transition: all 0.3s ease;
      &:hover {
         background: #fcec41;
         transition: all 0.3s ease;
      }
      @media (max-width: 992px) {
         flex: 0 1 20%;
      }
      @media (max-width: 600px) {
         flex: 0 1 25%;
      }
      @media (max-width: 480px) {
         flex: 0 1 45%;
      }
      @media (max-width: 350px) {
         flex: 0 1 100%;
      }
   }
   &__item-text {
      margin-bottom: 16px;
   }
   &__item-img {
      width: 100%;
      img {
         width: 100%;
      }
   }
}
