.section {
   &__list {
   }
}
.list {
   &__header {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 28px;
   }
   &__header-item {
      padding: 4px 8px;
      margin-right: 8px;
      margin-bottom: 8px;
      background: #ffffff;
      border: 1px solid #d1d1d3;
      transition: all 0.3s ease;
      cursor: pointer;
      &.active,
      &:hover {
         background: #fbe303;
         transition: all 0.3s ease;
      }
   }
   &__item {
      border-bottom: 1px solid #e8e9ea;
      padding-bottom: 28px;
      margin-bottom: 28px;
      display: flex;
      @media (max-width: 768px) {
         margin-bottom: 18px;
         padding-bottom: 18px;
      }
      @media (max-width: 400px) {
         margin-bottom: 10px;
         padding-bottom: 10px;
      }
   }
   &__title {
      min-width: 180px;
      max-width: 180px;
      width: 100%;
      @media (max-width: 1172px) {
         min-width: 80px;
         max-width: 80px;
      }
      @media (max-width: 992px) {
         min-width: 30px;
         max-width: 30px;
      }
      @media (max-width: 768px) {
         min-width: 80px;
         max-width: 80px;
      }
   }
   &__content {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      width: 100%;
      gap: 12px;
      @media (max-width: 992px) {
         grid-template-columns: repeat(4, 1fr);
      }
      @media (max-width: 768px) {
         grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 480px) {
         grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 400px) {
         grid-template-columns: repeat(1, 1fr);
      }
      li {
         img {
            opacity: 0;
            margin-right: 4px;
            transition: all 0.3s ease;
            @media (max-width: 768px) {
               display: none;
            }
         }
         &:hover {
            img {
               opacity: 1;
               transition: all 0.3s ease;
            }
         }
      }
   }
}
