.orders {
   @media (max-width: 768px){
      display: flex;
      flex-wrap: wrap;  
      justify-content: center;
   }
   &__item {
      background: #ffffff;
      border: 1px solid #e8e9ea;
      display: flex;
      margin-bottom: 16px;
      @media (max-width: 768px){
         flex-direction: column;
         flex: 0 1 45%;
         margin-right: 16px;
      }
      @media (max-width: 480px){
         flex: 0 1 100%;
         margin-right: 0;
      }
   }
   &__item-img {
      width: 180px;
      height: 100%;
      img {
         width: 180px;
         height: 100%;
         object-fit: cover;
      }
      @media (max-width: 992px){
         width: 150px;
         display: flex;
         align-items: center;
         justify-content: center;
         img{
            width: 150px;
         }  
      }
      @media (max-width: 768px){
         width: 100%;
         img{
            width: 100%;
         }
      }
   }
   &__item-content {
      background: #ffffff;
      border-left: 1px solid #e8e9ea;
      padding: 16px;
      &:nth-child(2) {
         width: 100%;
      }
      &:nth-child(3) {
         display: flex;
         max-width: 392px;
         width: 100%;
         @media (max-width: 768px){
            max-width: 100%;
         }
         @media (max-width: 600px){
            flex-direction: column;
         }
      }
   }
   &__item-title {
      margin-bottom: 12px;
   }
   &__item-price {
      margin-bottom: 12px;
   }
   &__item-text {
      margin-bottom: 4px;
   }
   &__item-container {
      margin-right: 57px;
      @media (max-width: 992px){
         margin-right: 16px;
      }
      @media (max-width: 600px){
         margin-right: 0;
         margin-bottom: 16px;
      }
      &:last-child {
         margin-right: 0;
      }
   }
   &__item-subtitle {
   }
}
