.card {
   &__body {
      display: flex;
      align-items: center;
      border: 1px solid #e8e9ea;
      margin-bottom: 40px;
      @media (max-width: 768px){
         flex-direction: column;
      }
   }
   &__img {
      width: 100%;
      height: 100%;
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   &__content {
      background: #ffffff;
      border-left: 1px solid #e8e9ea;
      padding: 24px;
      @media (max-width: 400px){
         padding: 12px;
      }
      .card__text {
         margin-bottom: 12px;
         color: #454950 !important;
      }
   }
   &__title {
      @media (max-width: 400px){
         font-size: 24px !important; 
      }
   }
   &__content-buy{
      display: flex;
      align-items: center;
      margin-bottom: 35px;
      @media (max-width: 992px){
         flex-direction: column;
         align-items: flex-start;
      }
      @media (max-width: 768px){
         flex-direction: row;
      }
      @media (max-width: 400px){
         flex-direction: column;
      }
   }
   &__btn{
      padding: 12px 80px !important;
      @media (max-width: 600px){
         padding: 12px 24px !important;
      }
      @media (max-width: 400px){
         width: 100%;
         display: flex;
         justify-content: center;
      }
   }
   .btn__form{
      margin-left: 12px;
      display: flex !important;
      max-width: 144px;
      width: 100%;
      @media (max-width: 992px){
         margin-left: 0;
         max-width: 100%;
         margin-top: 16px;
      }
      @media (max-width: 768px){
         max-width: 144px;
         margin-left: 12px;
         margin-top: 0;
      }
      @media (max-width: 400px){
         max-width: 100%;
         margin-left: 0;
         margin-top: 16px;
      }
   }
   &__status {
      display: flex;
      align-items: center;
      margin: 12px 0;
      img {
         width: 16px;
         height: 16px;
         margin-right: 5px;
      }
   }
   &__status-text {
      color: #3baa35 !important;
   }
   &__price {
      margin-bottom: 24px;
   }
   &__text {
      margin-bottom: 20px;
   }
   &__item {
      display: flex;
      align-items: flex-start;
      .card__text {
         margin-bottom: 0;
         margin-left: 12px;
      }
   }
   &__container {
      background: #ffffff;
      border: 1px solid #e8e9ea;
      border-radius: 10px;
      padding: 60px 200px;
      @media (max-width: 992px){
         padding: 40px 100px;
      }
      @media (max-width: 768px){
         padding: 36px;
      }
      @media (max-width: 600px){
         padding: 24px;
      }
      @media (max-width: 480px){
         padding: 10px;
      }
      .card__title{
         margin-bottom: 20px;
      }
   }
}
