.section {
   &__katalog {
   }
}
.katalog {
   &__header {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 80px;
   }
   &__header-item {
      padding: 8px 12px;
      margin-right: 8px;
      margin-bottom: 8px;
      background: #ffffff;
      border: 1px solid #d1d1d3;
      transition: all 0.3s ease;
      cursor: pointer;
      &.active,
      &:hover {
         background: #fbe303;
         transition: all 0.3s ease;
      }
   }
   &__items{
      display: flex;
      flex-wrap: wrap;
   }
   &__item{
      flex: 0 1 25%;
      margin-bottom: 80px;
      padding-right: 16px;
      @media (max-width: 992px){
         flex: 0 1 33%;
      }
      @media (max-width: 768px){
         flex: 0 1 50%;
         margin-bottom: 40px;
      }
      @media (max-width: 480px){
         flex: 0 1 100%;
      }
   }
   &__item-title{
      max-width: 180px;
      width: 100%;
      text-align: center;
      margin-bottom: 16px;
   }
   &__item-img{
      margin-bottom: 20px;
      max-width: 180px;
      width: 100%;
      img{
         width: 100%;
      }
   }
   &__item-list{
      li{
         display: flex;
         margin-bottom: 12px;
         align-items: center;
         &:last-child{
            margin-bottom: 0;
         }
         img{
            opacity: 0;
            margin-right: 4px;
            transition: all .3s ease;
         }
         &:hover{
            img{
               opacity: 1;
               transition: all .3s ease;
            }
         }
      }
   }
}
