.section {
   &__documentation {
   }
}
.documentation {
   &__title {
      margin-bottom: 40px;
      @media (max-width: 480px){
         margin-bottom: 20px;
      }
   }
   &__items {
      display: flex;
      justify-content: center;
      @media (max-width: 992px){
         flex-wrap: wrap;
      }
   }
   &__item {
      margin-right: 16px;
      @media (max-width: 992px){
         flex: 0 1 45%;
         margin-bottom: 16px;
         &:last-child{
            margin-bottom: 0;
         }
      }
      @media (max-width: 600px){
         @media (max-width: 600px){
            flex: 0 1 100%;
         }
      }  
      &:last-child{
         margin-right: 0;
      }
   }
   &__item-img {
      width: 100%;
      img{
         width: 100%;
      }
   }
   &__item-content {
      padding: 16px;
   }
   &__item-title {
      margin-bottom: 4px;
   }
   &__item-text {
      margin-bottom: 16px;
   }
   &__item-btn {
      width: 100%;
      display: flex;
      justify-content: center;
   }
}
