.items {
   &__title {
      margin-bottom: 40px;
   }
   &__body {
      display: flex;
   }
   &__aside {
      margin-right: 16px;
      border: 1px solid #e8e9ea;
      min-width: 278px;
      max-width: 278px;
      width: 100%;
      @media (max-width: 992px){
         position: fixed;
         left: -100%;
         top: 0;
         background: #fff;
         height: 100%;
         bottom: 0;
         z-index: 10000;
         overflow-y: scroll;
         transition: all .3s ease;
         &.active{
            left: 0;
            transition: all .3s ease;
         }
      }
      @media (max-width: 400px){
         min-width: fit-content;
         max-width: 100%;
      }
   }
   &__aside-container {
      background: #ffffff;
      border: 1px solid #f6f6f6;
      padding: 16px;
      @media (max-width: 992px){
         margin-top: 50px;
      }
   }
   &__aside-title {
      margin-bottom: 12px;
   }
   &__aside-form {
      margin-bottom: 32px;
      display: flex;
      flex-wrap: wrap;
      width: 100%;
   }
   &__aside-input {
      background: #f6f6f6;
      border: none;
      padding: 12px 16px;
      margin-right: 8px;
      border-bottom: 1px solid #d1d1d3;
      flex: 0 1 45%;
      width: 100%;
      margin-bottom: 12px;
      color: #8b8d92;
      &::placeholder {
         color: #8b8d92;
      }
      &::-moz-placeholder {
         color: #8b8d92;
      }
      &:last-child {
         margin-right: 0;
      }
   }
   &__aside-range {
      box-sizing: border-box;
      appearance: none;
      width: 100%;
      margin: 0;
      overflow: hidden;
      border: 0;
      outline: none;
      background: linear-gradient(#e8e9ea, #e8e9ea) no-repeat center;
      background-size: 100% 8px;
      pointer-events: none;
      position: absolute;
      z-index: 2;
      &::-webkit-slider-thumb {
         height: 20px;
         width: 20px;
         background: #ffffff;
         z-index: 2;
         border: 1px solid #5d6066;
         position: relative;
         cursor: pointer;
         appearance: none;
         pointer-events: all;
         &::before,
         &::after {
            content: "";
            position: absolute;
            top: 6px;
            bottom: 6px;
            height: 8px;
            width: 1px;
            background: #5d6066;
         }
         &::before {
            left: 6px;
         }
         &::after {
            right: 6px;
         }
      }
   }
   .multi-range {
      position: relative;
      height: 20px;
      flex: 0 1 100%;
   }
   &__aside-search {
      width: 100%;
      flex: 0 1 100%;
      display: flex;
      align-items: center;
      margin-bottom: 12px;
      .items__aside-input {
         width: 100%;
         flex: 0 1 100%;
      }
      button {
         border: none;
         cursor: pointer;
      }
   }
   &__burger{
      margin-right: 20px;
      display: none;
      @media (max-width: 992px){
         display: block;
         transition: all .3s ease;
         &.active{
            padding: 16px 0 10px 16px;
            transition: all .3s ease;
            z-index: 10000;
            position: fixed;
            left: 0;
            top: 0;
            background: #fff;
            max-width: 278px;
            width: 100%;
            @media (max-width: 400px){
               max-width: 100%;
            }
         }
      }
   }
   &__aside-items {
      height: 300px;
      overflow-y: scroll;
      display: block !important;
      width: 100%;
      .custom-checkbox > input {
         position: absolute;
         z-index: -1;
         opacity: 0;
      }
      .checkbox {
         margin-bottom: 12px;
      }
      .custom-checkbox > span {
         display: inline-flex;
         align-items: center;
         user-select: none;
      }
      .custom-checkbox > span::before {
         content: "";
         display: inline-block;
         width: 18px;
         height: 18px;
         border: 2px solid #454950;
         margin-right: 8px;
         border-radius: 5px;
         background-repeat: no-repeat;
         background-position: top right;
         background-size: 70% 70%;
      }
      .custom-checkbox > input:checked + span::before {
         background-image: url("..//img/checkbox.svg");
      }
   }
   &__aside-select {
      position: relative;
      width: 100%;
      select {
         display: none;
      }
      .items__aside-selected {
         background: #f6f6f6;
         padding: 12px 16px;
         border-bottom: 1px solid #d1d1d3;
         &::after {
            content: url(..//img/select-arrow.svg);
            position: absolute;
            right: 16px;
         }
      }
      .items__aside-arrow:after {
         transform: rotate(180deg);
         top: 7px;
      }
      div,
      .items__aside-selected {
         cursor: pointer;
         user-select: none;
      }
      .items__aside-content {
         position: absolute;
         top: 100%;
         left: 0;
         right: 0;
         z-index: 99 !important;
         background: #f6f6f6;
         border-bottom: 1px solid #d1d1d3;
         div {
            padding: 12px 16px;
         }
      }
      .items__aside-hide {
         display: none;
      }
   }
   &__aside-container {
      display: flex;
      flex-direction: column;
      background: #ffffff;
      border-top: 1px solid #e8e9ea;
   }
   &__aside-btn {
      display: flex;
      justify-content: center;
      margin-bottom: 12px;
      &:last-child {
         margin-bottom: 0;
      }
   }
   &__container {
   }
   &__sort {
      display: flex;
      align-items: center;
      margin-bottom: 16px;
      flex-wrap: wrap;
      .items__text {
         color: #8b8d92;
         margin-right: 20px;
         &:last-child {
            margin-right: 0;
         }
         &.active {
            color: #171b24;
         }
      }
   }
   &__text {
   }
   &__items {
      display: flex;
      flex-wrap: wrap;
      @media (max-width: 992px){
         justify-content: center;
      }
   }
   &__item {
      flex: 0 1 30% !important;
      margin-right: 16px;
      margin-bottom: 16px;
      background: #ffffff;
      border: 1px solid #e8e9ea;
      padding-right: 0 !important;
      @media (max-width: 768px) {
         flex: 0 1 45% !important;
      }
      @media (max-width: 480px) {
         flex: 0 1 100% !important;
         margin-right: 0;
      }
   }
   &__item-img {
      width: 100%;
      img {
         width: 100%;
      }
   }
   &__item-content {
      padding: 16px;
      @media (max-width: 768px) {
         padding: 10px;
      }
   }
   &__item-text {
      height: 72px;
      margin-bottom: 12px;
      overflow-y: hidden;
   }
   &__item-price {
      margin-bottom: 16px;
   }
   &__item-btn {
      display: flex;
      justify-content: center;
   }
}
