.section {
   &__goods {
   }
}
.goods {
   &__title {
      margin-bottom: 20px;
   }
   &__items {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
   }
   &__item {
      flex: 0 1 23% !important;
      width: 100%;
      background: #ffffff;
      border: 1px solid #e8e9ea;
      margin: 8px !important;
      padding-right: 0 !important;
      @media (max-width: 992px){
         flex: 0 1 31% !important;
      }
      @media (max-width: 768px){
         flex: 0 1 46% !important;
      }
      @media (max-width: 480px){
         flex: 0 1 100% !important;
      }
   }
   &__item-img {
      width: 100%;
      img{
         width: 100%;
      }
   }
   &__item-content {
      padding: 16px;
      @media (max-width: 768px){
         padding: 16px 10px 10px;
      }
   }
   &__item-text {
      height: 72px;
      overflow-y: hidden;
      margin-bottom: 12px;
   }
   &__item-price {
      margin-bottom: 16px;
   }
   &__item-btn {
      width: 100%;
      display: flex;
      justify-content: center;
   }
}
