*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative;
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column !important;
	overflow-x: hidden !important;
	font-style: normal;
	opacity: 0;
	transition: all 0.3s ease;
	&.loaded {
		opacity: 1;
		transition: all 0.3s ease;
	}
}

main {
	flex: 1 1 auto;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	list-style: none;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:focus-visible {
	outline: none;
}
.container {
	max-width: $mw;
	margin: 0 auto;
	@media (max-width: $md1) {
		max-width: 950px;
	}
	@media (max-width: $md2) {
		max-width: 750px;
	}
	@media (max-width: $md3) {
		max-width: none;
		padding: 0 20px;
	}
	@media (max-width: 350px) {
		padding: 0 10px;
	}
}
// main style
.title {
	font-family: "Manrope";
	font-weight: 700;
	font-size: 48px;
	line-height: 120%;
	color: #2f323a;
	@media (max-width: 480px) {
		font-size: 36px;
	}
	@media (max-width: 350px) {
		font-size: 30px;
	}
}
.title-big {
	font-family: "Manrope";
	font-weight: 600;
	font-size: 34px;
	line-height: 130%;
	color: #2f323a;
	@media (max-width: 480px) {
		font-size: 26px;
	}
}
.subtitle {
	font-family: "Manrope";
	font-weight: 600;
	font-size: 24px;
	line-height: 130%;
	color: #2f323a;
}
.subtitle-big {
	font-family: "Manrope";
	font-weight: 400;
	font-size: 20px;
	line-height: 140%;
	color: #2f323a;
	@media (max-width: 480px) {
		font-size: 18px;
	}
	&.bold {
		font-weight: 700;
	}
}
.text {
	font-family: "Manrope";
	font-weight: 500;
	font-size: 18px;
	line-height: 136%;
	color: #2f323a;
	@media (max-width: 480px) {
		font-size: 16px;
	}
	&.bold {
		font-weight: 700;
	}
}
.text-big {
	font-family: "Manrope";
	font-weight: 400;
	font-size: 16px;
	line-height: 150%;
	color: #2f323a;
	&.bold {
		font-weight: 500;
	}
}
.subtext {
	font-family: "Manrope";
	font-weight: 500;
	font-size: 15px;
	line-height: 145%;
	color: #2f323a;
}
.btn {
	background: #fcec41;
	padding: 12px 0;
	transition: all 0.3s ease;
	path {
		transition: all 0.3s ease;
	}
	&:hover {
		background: #2f323a;
		transition: all 0.3s ease;
		color: #fff;
		path {
			stroke: #fff;
			transition: all 0.3s ease;
		}
	}
	&-white {
		background: #ffffff;
		border: 1px solid #d1d1d3;
	}
}
.header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 16px;
}
.header__btn {
	background: #ffffff;
	border: 1px solid #d1d1d3;
	padding: 12px 24px;
	display: inline-flex;
	align-items: center;
	transition: all 0.3s ease;
	svg {
		margin-left: 8px;
	}
	path {
		transition: all 0.3s ease;
	}
	&:hover {
		background: #2f323a;
		color: #ffffff;
		path {
			stroke: #ffffff;
			transition: all 0.3s ease;
		}
	}
}
.section {
	padding: 40px 0;
}
.btn-shop {
	&.hidden {
		display: none;
	}
}
.btn__form {
	display: flex;
	align-items: center;
	width: 100%;
	height: 48px;
	display: none;
	&.show {
		display: flex;
	}
}
.btn__form-count {
	min-width: 48px;
	max-width: 48px;
	width: 100%;
	height: 100%;
	background: #ffffff;
	border: 1px solid #d1d1d3;
	position: relative;
	transition: all 0.3s ease;
	cursor: pointer;
	&:hover {
		background: #2f323a;
		transition: all 0.3s ease;
		&::after,
		&::before {
			background: #ffffff;
			transition: all 0.3s ease;
		}
	}
	&::after,
	&::before {
		content: "";
		position: absolute;
		transform: translate(-50%, -50%);
		left: 50%;
		top: 50%;
		background: #454950;
		transition: all 0.3s ease;
	}
	&.plus {
		&::after {
			width: 14px;
			height: 2px;
		}
		&::before {
			width: 2px;
			height: 14px;
		}
	}
	&.minus {
		&::after {
			width: 14px;
			height: 2px;
		}
	}
}
.btn__form-input {
	background: #ffffff;
	border-width: 1px 0px;
	border-style: solid;
	border-color: #d1d1d3;
	width: 100%;
	height: 100%;
	-webkit-appearance: none;
	appearance: none;
	text-align: center;
	-moz-appearance: textfield;
	&::-webkit-inner-spin-button {
		display: none;
	}
}
.breadcrupms {
	margin-bottom: 20px;
	&__text {
		color: #454950 !important;
		a {
			color: #454950;
		}
	}
}
