.cart {
   display: flex;
   @media (max-width: 992px){
      flex-direction: column-reverse;
   }
   &__aside {
      max-width: 278px;
      width: 100%;
      margin-right: 16px;
      @media (max-width: 992px){
         margin: 0 auto;
         max-width: 100%;
      }
   }
   &__aside-container {
      border: 1px solid #e8e9ea;
      padding: 16px;
      &:nth-child(2) {
         margin-bottom: 16px;
      }
   }
   &__aside-title {
      margin-bottom: 12px;
   }
   &__aside-block {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 12px;
   }
   &__aside-btn {
      width: 100%;
      display: flex;
      justify-content: center;
   }
   &__aside-item {
      display: flex;
      align-items: flex-start;
      margin-bottom: 16px;
      img {
         margin-right: 10px;
      }
   }
   &__body {
      width: 100%;
   }
   &__item {
      background: #ffffff;
      border: 1px solid #e8e9ea;
      width: 100%;
      display: flex;
      align-items: center;
      margin-bottom: 12px;
      @media (max-width: 768px){
         flex-wrap: wrap;
         align-items: center;
         justify-content: flex-end;
      }
      @media (max-width: 480px){
         flex-direction: column;
      }
      .btn__form{
         display: flex !important;
      }
   }
   &__item-img {
      max-width: 278px;
      width: 100%;
      height: 100%;
      @media (max-width: 768px){
         flex: 0 1 50%;
         max-width: 100%;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   &__item-container {
      background: #ffffff;
      border-left: 1px solid #e8e9ea;
      padding: 16px;
      max-width: 294px;
      width: 100%;
      @media (max-width: 768px){
         flex: 0 1 50%;
         max-width: 100%;
      }
   }
   &__item-info{
      height: 112px;
      margin-bottom: 16px;
   }
   &__item-title{
      margin-bottom: 12px;
   }
   &__item-btn{
      display: flex;
      justify-content: center;
      width: 100%;
   }
}
